// eslint-disable-next-line import/no-unresolved
import MusicsPage from '../musicsPage/index';
import ImgsWall from '../imgsWall/index';
import CardsApi from '@/api/titleCard';
import MainPage from '../main/index';
import SingerDetail from '../singerInfo/index';
import Tabs from '@/components/common-bottom-tabs';
import TabsList from '@/data/tabs';
import Card from '@/components/article';
// import { Loading } from 'element-ui';


export default {
  name: 'sasa',
  components: {
    Tabs,
    Card,
    SingerDetail,
    MainPage,
    ImgsWall,
    MusicsPage,
  },
  data() {
    return {
      isShowCtrl: false,
      musicSrc: {
        title: '这是第一首',
        src: 'http://106.14.175.12:8090/music/1.mp3',
        index: 0,
      },
      isShowPlayer: false,
      playing: false,
      tab: 0,
      isFocus: false,
      loading: false,
      musicArr: [
        {
          title: '这是第一首',
          src: 'http://106.14.175.12:8090/music/1.mp3',
          index: 0,
        },
        {

          title: '这是第二首歌',
          src: 'http://106.14.175.12:8090/music/2.mp3',
          index: 1,
        },
        {
          title: '这是第三首歌',
          src: 'http://106.14.175.12:8090/music/1.mp3',
          index: 2,
        },

      ],
      list1: [],
      list: [
      ],
    };
  },
  mounted() {
    this.feacthPage();
    this.getCards();
  },
  methods: {
    feacthPage() {
      this.list = TabsList; // tabs list
      console.log(this.$route.params.tab)
      if(this.$route.params.tab){
        this.tab=this.$route.params.tab
      }
    },
    showCtrl() {
      this.isShowCtrl = !this.isShowCtrl;
    },
    goDetail(item) {
      this.$router.push({
        name:'Detail',
        params:{
          article:item
        }
      })
    },
    pausePlayer() {
      const pause = document.getElementById('video');
      if (pause.paused == true) {
        pause.play();
        this.playing = true;
      } else {
        pause.pause();
        this.playing = false;
      }
    },
    onAMusic(i) {
      if (i > 0) {
        this.musicSrc = this.musicArr[i - 1];
        const pause = document.getElementById('video');
        pause.load();
      }
    },
    nextMusic(i) {
      if (i < this.musicArr.length) {
        this.musicSrc = this.musicArr[i + 1];
        const pause = document.getElementById('video');
        pause.load();
      }
    },
    ImgMouseOver() {
      if (this.isShowPlayer == true) {
        this.isShowPlayer = false;
      } else {
        this.isShowPlayer = true;
      }
      // setTimeout(() => {
      //   this.isShowPlayer=false
      // }, 6000);
    },
    ImgMouseOut() {
      setTimeout(() => {
        this.isShowPlayer = false;
      }, 10000);
    },
    getCards() {
      CardsApi.CardsApi().then((res) => {
        console.log(res);
        this.list1 = res.arr;
      });
    },
    goNext() {
      this.loading = true;
      this.getCards();
      setTimeout(() => {
        this.loading = false;
      }, 2000);
    },
    tabEmit(e) {
      console.log(e);
      this.loading = true;
      setTimeout(() => {
        this.tab = parseInt(e);
        this.loading = false;
      }, 1000);
    },
    inputFocus() {
      this.isFocus = true;
    },
    inputBlur() {
      this.isFocus = false;
    },
  },
};



