import Tabs from '@/components/common-bottom-tabs';
import TabsList from '@/data/tabs';

export default {
components:{
  Tabs
},
data(){
  return {
    title:'',
    date:'',
    content:'',
    list:[],
    loading:false
  }
},
mounted() {
  this.list=TabsList
  console.log(this.$route.params)
  this.title=this.$route.params.article.title
  this.content=this.$route.params.article.content
  this.date=this.$route.params.article.date
},
methods: {
   tabEmit(e) {
      console.log(e);
      this.loading = true;
      setTimeout(() => {
        this.tab = parseInt(e);
        this.loading = false;
        this.$router.push({
          name:'MainPage',
          params:{
            tab:this.tab
          }
        })
      }, 1000);
    },
},
};



